
.header_fluid{
	width: 100%;
	margin-top: 20px;
	background-color: #fff;
}
.header_fluid a,.body_fluid a,.replies a{
	text-decoration: none;
	color: #08d;
}
.header_fluid .header{
	padding: 80px 0 0 20px;
}
.header_fluid .paths{
	display: block;
	padding: 20px 10px;
	position: fixed;
	width: 100%;
	top: 50px;
	z-index: 2;
	white-space: nowrap;
	background-color: #ccc;
}
.header_fluid .paths li{
	display: inline-block;
	color: #666;
}
.header_fluid .header h3{
	font-size: 22px;
}
.header_fluid .header .topic_label{
	float: left;
	color: #fff;
	padding: 2px;
	margin-top: 5px;
	font-size: 16px;
	font-weight: 700;
	border-radius: 5px;
	background-color: #76d2a2;
}
.header_fluid .header .changes{
	color: #838383;
	margin-top: 10px;
	padding-bottom: 10px;
	border-bottom: 1px solid #dcdcdc;
}
.header_fluid .header .changes span,.header_fluid .header .changes span a{
	font-size: 12px;
}
.body_fluid{
	background-color: #fff;
	padding-bottom: 20px;
}
.markdown-text{
	padding: 10px 20px 20px;
}
.markdown-text h3,.markdown-text h2,.markdown-text h1{
	border-bottom: 1px solid #dcdcdc;
}
.markdown-text ul{
	padding: 0;
	margin: 0 0 10px 25px;
}
.markdown-text ul>li{
	line-height: 2em;
	font-size: 14px;
}
.markdown-text p,{
	font-size: 14px;
	line-height: 1.7;
}
.markdown-text img{
	max-width: 100%;
}
.replies{
	background-color: #fff
}
.replies .re_top{
	background-color: #ccc;
	padding: 10px;
}
.reply{
	padding: 5px 10px 0px;
	border-bottom: 1px solid #dcdcdc;
}
.reply .author .photo{
	width: 40px;
	vertical-align: middle;
}
.reply .author a span{
	font-size: 12px;
	text-decoration: none;
}
.reply .author a .name{
	color: #838383;
}
.reply .action{
	float: right;
	color: #747474;
	font-size: 12px;
	margin-top: 10px;
}
.reply .up_btn{
	cursor: pointer;
}
.reply .show{
	display: none;
}



