
.markdown-text h2{
	line-height: 40px;
	font-size: 26px;
	margin: 30px 0 15px;
	border-bottom: 1px solid #eee;
	font-weight: 700;
	font-family: inherit;
}
.markdown-text h3{
	font-size: 24px;
	margin: 25px 0 15px;
}
.markdown-text h4{
	margin: 20px 0 10px;
}
.markdown-text p{
	font-size: 15px;
	line-height: 1.7em;
	margin: 1em 0;
	overflow: auto;
	word-wrap: break-word;
	white-space: pre-wrap;
}
.topic_content{
	margin-top: 110px;
	overflow: hidden;
	background-color: #fff;
}
.topic_content a{
	text-decoration: none;
	color: #08d;
}



